import { GoogleLogin } from "react-google-login";

function Login({ onLoginSuccess, onLoginFailure, clientId }) {
    return (
        <div id="signInButton">
            <div>
                <h1>Todo App</h1>
                <GoogleLogin
                    clientId={clientId}
                    buttonText="Login"
                    onSuccess={onLoginSuccess}
                    onFailure={onLoginFailure}
                    cookiePolicy={"single_host_origin"}
                    isSignedIn={true}
                />
            </div>
        </div>
    )
}

export default Login;