import { GoogleLogout } from "react-google-login";


function Logout({ onLogoutSuccess, clientId }) {


    return (
        <div id="signOutButton">
            <div>
                <h1>Todo App</h1>
                <GoogleLogout
                    clientId={clientId}
                    buttonText="Logout"
                    onLogoutSuccess={onLogoutSuccess}
                />
            </div>
        </div>
    )
}

export default Logout;