import { useHistory } from "react-router-dom";
import loadingicon from "./assets/loading.gif";

const Home = ({ userId, todos, completeTodo, deleteTodo, newTodo, setNewTodo, addTodo }) => {
    const history = useHistory();

    return (
        <div id="home">
            <h1 style={{ opacity: 0.7, marginBottom: "0.5rem", textAlign: "center" }}>Todo App</h1>
            <div>
                <textarea type="text" value={newTodo} onInput={function (e) {
                    e.target.style.height = "";
                    e.target.style.height = Math.min(e.target.scrollHeight, 160) + "px";
                    setNewTodo(e.target.value);
                }}></textarea>
                <button onClick={addTodo}>Add Todo</button>
            </div>
            <h2 style={{ opacity: 0.5, margin: "1rem 0rem" }}>Todos</h2>
            {
                !todos ?
                    <div className="loader">
                        <img src={loadingicon} alt="loader-icon" />
                    </div> :
                    <table>
                        <tbody>
                            {
                                todos.map(todo =>
                                    <tr key={todo._id}>
                                        <td>
                                            <input type="checkbox" style={{ marginRight: "0.5rem", width: "16px", height: "16px" }} checked={todo.complete} onChange={() => completeTodo(todo)}></input>
                                        </td>
                                        <td style={{ width: "100%", textDecoration: todo.complete ? "line-through" : "" }}>{todo.text}</td>
                                        <td className="delete" onClick={() => deleteTodo(todo)}><img src="https://img.icons8.com/plasticine/100/000000/filled-trash.png" alt="delete-icon" width={"28px"} /></td>
                                    </tr>
                                )
                            }
                        </tbody>
                    </table>
            }
            <div style={{ position: "absolute", bottom: 0, left: 0, textAlign: "center", width: "100%", paddingBottom: "0.5rem" }}>
                <a href="#logout">Logout</a>
            </div>
        </div>
    )
}

export default Home;