import './App.css';
import Login from './Login';
import Logout from './Logout';
import { useEffect, useState } from 'react';
import { gapi } from 'gapi-script';
import { Route, Switch, useHistory } from "react-router-dom";
import Home from './Home';

const BASE_URL = "https://todoapp.hp01.me/todos/";
const clientId = "486292307292-8lv45rl1bokc0n360aabtgatc6tkcmui.apps.googleusercontent.com";

function App() {
  const history = useHistory();
  const [userId, setUserId] = useState(undefined);
  const [todos, setTodos] = useState(undefined);
  const [newTodo, setNewTodo] = useState("");

  useEffect(() => {
    if (history.location.pathname !== "/login")
      history.push("/login");
    function start() {
      gapi.client.init({ clientId: clientId, scope: "https://www.googleapis.com/auth/userinfo.profile" });
    }
    gapi.load("client:auth2", start);
  }, [history]);

  useEffect(function () {
    if (userId) getTodos();
  }, [userId]);

  function onLoginSuccess(res) {
    setUserId(res.profileObj.googleId);
    if (history.location.pathname !== "/")
      history.push("/");
  }
  
  function onLoginFailure(res) {
    history.push("/login");
  }

  function onLogoutSuccess(res) {
    history.push("/login");
  }

  function getTodos() {
    fetch(BASE_URL, { headers: { Authorization: userId } })
      .then(res => res.json())
      .then(data => {
        data.sort((a, b) => a.complete - b.complete);
        setTodos(data);
      }).catch(err => {
        history.push("/login");
      });
  }

  async function completeTodo(todo) {
    todo.complete = !todo.complete;
    fetch(BASE_URL + todo._id, {
      method: "PUT",
      headers: { 'Content-Type': 'application/json', Authorization: userId },
      body: JSON.stringify({ complete: todo.complete })
    }).catch(err => history.push("/login"));
    todos.sort((a, b) => a.complete - b.complete);
    setTodos([...todos]);
  }

  async function deleteTodo(todo) {
    setTodos([...todos.filter(item => item._id !== todo._id)]);
    fetch(BASE_URL + todo._id, {
      method: "DELETE",
      headers: { Authorization: userId }
    }).catch(err => history.push("/login"));
  }

  async function addTodo(e) {
    fetch(BASE_URL, {
      method: "POST",
      headers: { 'Content-Type': 'application/json', Authorization: userId },
      body: JSON.stringify({ text: newTodo })
    }).then(res => res.json())
      .then(data => {
        setNewTodo("");
        todos.unshift(data);
        setTodos([...todos]);
      }).catch(err => history.push("/login"));
  }

  return (
    <div className="App">
      <Switch>
        <Route exact path="/" render={(props) => <Home userId={userId} todos={todos} setTodos={setTodos} completeTodo={completeTodo}
          deleteTodo={deleteTodo} addTodo={addTodo} newTodo={newTodo} setNewTodo={setNewTodo} />}></Route>
        <Route exact path="/login" render={(props) =>
          <Login onLoginSuccess={onLoginSuccess} onLoginFailure={onLoginFailure} clientId={clientId} />
        }></Route>
        <Route exact path="/logout" render={(props) => <Logout onLogoutSuccess={onLogoutSuccess} clientId={clientId} />}></Route>
      </Switch>
    </div>
  );
}

export default App;
